.feature-box {
    background-color: rgba($color-white, .4);
    height: 30rem;
    font-size: 1.5rem;
    padding: 2.5rem;
    text-align: center;
    border-radius: 3px;
    box-shadow: 0 2.5rem 4rem rgba($color-black, .15);
    transition: transform .3s;
    backface-visibility: hidden;

    &_icon {
        font-size: 6rem;
        margin-bottom: .5rem;
        display: inline-block;
        color: $color-black; }
    &:hover {
        transform: translateY(-1.5rem) scale(1.2); } }
